.containerKeyboard {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.keyboard button {
  border: none;
  background-color: #d3d6da;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  font-weight: bold;
  padding: 15px 12px;
  margin: 3px;
  transition: .5s;
  font-family: 'Lexipa', sans-serif;
}

.Enter {
  background-color: rgba(0, 128, 0, 0.3) !important;
}

.Backspace {
  background-color: rgba(255, 0, 0, 0.3) !important;
}